<template>
  <div class="app">
    <AppHeader fixed>
      <SidebarToggler class="d-lg-none" display="md" mobile />
      <b-link class="navbar-brand" to="#">
        <img class="navbar-brand-full" src="img/brand/logoImg.png" width="130" height="30">
        <img class="navbar-brand-minimized" src="img/brand/favicon.png" width="30" height="30">
      </b-link>
      <SidebarToggler class="d-md-down-none" display="lg" />
      <b-navbar-nav class="ml-auto">
        <DefaultHeaderDropdownAccnt/>
      </b-navbar-nav>
    </AppHeader>
    <div class="app-body">
      <AppSidebar fixed>
        <SidebarHeader/>
        <SidebarForm/>


        <!-- <SidebarNav :navItems="nav"></SidebarNav> -->
        <template>
          <nav class="sidebar-nav">
            <VuePerfectScrollbar class="scroll-area" :settings="psSettings" @ps-scroll-y="scrollHandle">
              <ul class="nav">
                <template v-for="(item, index) in nav" v-if="!item.visibility || item.visibility.includes(user.Role)">
                  <SidebarNavItem :key="index">
                    <SidebarNavLink :name="$t(item.name)" :url="item.url" :icon="item.icon" :badge="item.badge" :variant="item.variant" :attributes="item.attributes">
                    </SidebarNavLink>
                  </SidebarNavItem>
                </template>
              </ul>
            </VuePerfectScrollbar>
          </nav>
        </template>
        <SidebarFooter/>
        <SidebarMinimizer/>
      </AppSidebar>
      <main class="main">
        <template>
          <ol class="breadcrumb">
            <li class="breadcrumb-item" :key="index" v-for="(routeObject, index) in list">
              <span class="active" v-if="isLast(index)">{{ $t(getName(routeObject)) }}</span>
              <router-link :to="routeObject" v-else>{{ $t(getName(routeObject)) }}</router-link>
            </li>
          </ol>
        </template>

        <div class="container-fluid">
          <router-view></router-view>
        </div>
      </main>
    </div>
    <TheFooter>
      <!--footer-->
      <div>
        <a class="anchor-pointer">ToutPaie</a>
        <span class="ml-1">&copy; 2019 .</span>
      </div>
      <div class="ml-auto">
        <span class="mr-1">Powered by</span>
        <a class="anchor-pointer">ToutPaie</a>
      </div>
    </TheFooter>
  </div>
</template>

<script>
import nav from '@/_nav'
import i18n from '@/plugins/i18n';
import { Header as AppHeader, SidebarToggler, Sidebar as AppSidebar, SidebarFooter, SidebarForm, SidebarHeader, SidebarMinimizer, SidebarNav, Aside as AppAside, AsideToggler, Footer as TheFooter, Breadcrumb ,
      SidebarNavDivider,
    SidebarNavDropdown,
    SidebarNavLink,
    SidebarNavTitle,
    SidebarNavItem,
    SidebarNavLabel


} from '@coreui/vue'
import DefaultHeaderDropdownAccnt from './DefaultHeaderDropdownAccnt'
import * as toutPaieService from "../views/toutPaie/ToutPaieService.js";
import VuePerfectScrollbar from 'vue-perfect-scrollbar'

export default {
  name: 'DefaultContainer',
  components: {
    AsideToggler,
    AppHeader,
    AppSidebar,
    AppAside,
    TheFooter,
    Breadcrumb,
    DefaultHeaderDropdownAccnt,
    SidebarForm,
    SidebarFooter,
    SidebarToggler,
    SidebarHeader,
    SidebarNav,
    SidebarMinimizer,
    SidebarNavDivider,
    SidebarNavDropdown,
    SidebarNavLink,
    SidebarNavTitle,
    SidebarNavItem,
    SidebarNavLabel,
    VuePerfectScrollbar
  },
  data () {
    return {
      nav: nav.items,
      user : {},
      buildings : [],
      selectedBuilding : null,
    }
  },
  computed: {
    name () {
      return this.$route.name
    },
    list () {
      return this.$route.matched.filter((route) => route.name || route.meta.label )
    },
    psSettings: () => {
      // ToDo: find better rtl fix
      return {
        maxScrollbarLength: 200,
        minScrollbarLength: 40,
        suppressScrollX: getComputedStyle(document.querySelector('html')).direction !== 'rtl',
        wheelPropagation: false,
        interceptRailY: styles => ({ ...styles, height: 0 })
      }
    }
  },
  methods: {
    scrollHandle (evt) {
      // console.log(evt)
    },
    getName (item) {
      return item.meta && item.meta.label ? item.meta.label : item.name || null
    },
    isLast (index) {
      return index === this.list.length - 1
    }
  },
  beforeMount() {
    this.user = JSON.parse(window.localStorage.getItem('loginUser'));
  }
}
</script>

<style scoped lang="css">
  .scroll-area {
    position: absolute;
    height: 100%;
    margin: auto;
  }
</style>