<template>
  <div class="row">
    <div class="col-md-5">
      <AppHeaderDropdown right no-caret>
      <template slot="header">
        <label style="margin-right: 50px;">
          <b style="color: #ef402b;" v-on:click="showLang = true">Languages</b>
        </label>
      </template>
      <template slot="dropdown" v-if="showLang">
        <b-dropdown-item v-for="entry in languages" :key="entry.title" @click="changeLocale(entry.language)" href="#"><flag :iso="entry.flag" v-bind:squared=false /> {{$t(entry.title)}}</b-dropdown-item>
      </template>
    </AppHeaderDropdown>
    </div>
    <div class="col-md-7">
      <AppHeaderDropdown right no-caret >
        <template slot="header">
          <label v-on:click="showLang = false" style="cursor: pointer">
            <label></label>
            <!-- <b style="color: #ef402b;">Administrator</b> -->
            <b style="color: #ef402b;">{{loginUser.Users[0].FirstName+" "+loginUser.Users[0].LastName}}</b>
            <!-- <img style="height: 40px; width: 40px;"
              :src="userProfileThumbnail"
              class="img-avatar" onerror="this.onerror=null; this.src='img/brand/noImageAvailable.png'"
              alt="Profile" /> -->
          </label>
        </template>
        <template slot="dropdown" v-if="!showLang">
          <b-dropdown-item v-on:click="changePassword" ><i class="fa fa-lock" /> {{$t('Change Password')}}</b-dropdown-item>
          <b-dropdown-item v-on:click="logout" ><i class="fa fa-lock" /> {{$t('Logout')}}</b-dropdown-item>
        </template>
      </AppHeaderDropdown>
    </div>
  </div>
</template>

<script>
import { HeaderDropdown as AppHeaderDropdown } from '@coreui/vue'
import * as toutPaieService from "../views/toutPaie/ToutPaieService.js";
import i18n from '@/plugins/i18n';

export default {
  name: 'DefaultHeaderDropdownAccnt',
  components: {
    AppHeaderDropdown
  },
  data: () => {
    return {
      itemsCount: 42,
      userProfileThumbnail : "",
      loginUser :{},
      RememberToken:"",
      user : {},
      showLang : false,
      languages: [
        { flag: 'us', language: 'en', title: 'English' },
        { flag: 'fr', language: 'fr', title: 'French' }
      ]
    }
  },
  methods: {
    logout: function(building){
         this.$router.push('/login');
    },
    changePassword: function(building){
         this.$router.push('/reset-password?RememberToken='+this.RememberToken);
    },
    changeLocale(locale) {
      console.log(locale)
      window.localStorage.setItem("language",locale);
      i18n.locale = locale;
    },
  },
  beforeMount() {
    this.loginUser=JSON.parse(window.localStorage.getItem("loginUser"));
    this.RememberToken=this.loginUser.headers.RememberToken;
    // console.log("user details :",this.loginUser.headers.RememberToken);
  }
}
</script>
